.main-header {
    background: #091242;
    box-shadow: 0px -1px 0px 0px var(--primary-grey-white) inset;
}

.main-header__wrapper {
  margin: 0 auto;
  max-width: 1180px;
  padding: 24px 24px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.main-header__left-content {
    display: flex;
    gap: 80px;
    align-self: center;
}

.main-header__logo {
    color: #FFB82B;
    font-family: var(--font-family-rubik);
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-normal);
    line-height: 100%;
    letter-spacing: -0.417px;
    align-self: center;
}

.main-header__navigation {
    display: flex;
    gap: 80px;
    align-self: center;
}

.main-header__navigation-item {
    color: white;
    font-family: var(--font-family-rubik);
    font-size: var(--font-size-m);
    font-weight: var(--font-weight-light);
    line-height: 100%;
    letter-spacing: -0.417px;
    align-self: center;
}

.intro{
    height: 100vh;
    background: url(../assets/Background_Photo.png) no-repeat center;
    background-size: cover;
}